.header {
  padding: 0.9375rem 0.625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--darkPurple);
}
@media (min-width: 40em) {
  .header {
    flex-direction: row-reverse;
  }
}
.header__image img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  border: 2px solid var(--lightGray);
}
@media (min-width: 40em) {
  .header__image img {
    border: none;
    border-radius: 0;
    margin-left: 1.5625rem;
  }
}
.header__descript {
  padding-left: 0.625rem;
  text-align: start;
}
@media (min-width: 71.875em) {
  .header__descript {
    padding-left: 0;
    text-align: right;
  }
}
.header__descript h1 {
  font-size: 1.25rem;
  color: var(--lightGray);
}
.header__descript span {
  font-size: 1rem;
  color: var(--grayishWhite);
}

@media (min-width: 40em) {
  .main {
    margin: 1.5625rem;
  }
}
.main .card {
  margin: 0.9375rem;
}
.main .card__about {
  margin-bottom: 0.9375rem;
}
.main .card__about h2 {
  margin-bottom: 0.9375rem;
  color: var(--lightGray);
}
.main .card__about p {
  color: var(--grayishWhite);
}
.main .card__projects h3 {
  text-align: start;
  margin-bottom: 0.9375rem;
  color: var(--lightGray);
  font-size: 1.125rem;
}
.main .card__projects--projects {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (min-width: 40em) {
  .main .card__projects--projects {
    align-items: center;
    width: 90%;
    margin: auto;
    border-radius: 0.625rem;
    padding: 0.9375rem;
    box-shadow: 0px 5px 20px 0px rgba(255, 255, 255, 0.127);
    background-color: var(--blue-mediumS);
  }
}
.main .card__projects--projects .container {
  margin-bottom: 0.9375rem;
  flex: 50%;
  max-width: 50%;
  padding: 0 0.5rem;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container {
    position: relative;
    flex: 33.3%;
    max-width: 33.3%;
  }
  .main .card__projects--projects .container:hover {
    transform: scale(1.08);
    z-index: 1;
    transition: transform 0.3s ease;
  }
}
.main .card__projects--projects .container a {
  text-decoration: none;
}
.main .card__projects--projects .container a img {
  max-width: 100%;
  height: auto;
  margin-top: 0.5rem;
  box-shadow: 0px -10px 20px -5px #000000;
  border-top-left-radius: 0.3125rem;
  border-top-right-radius: 0.3125rem;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container a img {
    border-radius: 0.3125rem;
    box-shadow: none;
  }
  .main .card__projects--projects .container a img:hover {
    box-shadow: 0px 5px 20px 0px #000000;
    transition: box-shadow 0.3s ease;
  }
}
@media (min-width: 40em) {
  .main .card__projects--projects .container a:hover + .descript {
    opacity: 1;
  }
}
.main .card__projects--projects .container .descript {
  padding: 0.3125rem;
  margin-top: -0.25rem;
  text-align: center;
  font-size: 0.875rem;
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  background-color: var(--darkPurple);
  color: var(--textColor2);
  z-index: 1;
  box-shadow: 0px 0px 20px 2px #000000;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container .descript {
    position: absolute;
    bottom: 4px;
    width: calc(100% - 16px);
    height: 40px;
    font-size: 1rem;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: none;
  }
}
.footer {
  padding: 0.9375rem 0.625rem;
  position: relative;
  height: 170px;
  background-color: var(--darkPurple);
}
.footer__social {
  font-size: 1rem;
  text-align: center;
  color: var(--lightGray);
}
.footer__links {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.footer__links a {
  text-decoration: none;
  color: var(--textColor2);
  margin-right: 0.625rem;
}
.footer__links a span {
  font-size: 1.25rem;
  transition: all 0.2s ease;
}
.footer__links a .bi-github {
  color: var(--githubIcon);
}
.footer__links a .bi-github:hover {
  color: var(--githubIcon-hover);
}
.footer__links a .bi-linkedin {
  color: var(--linkedinIcon);
}
.footer__links a .bi-linkedin:hover {
  color: var(--linkedinIcon-hover);
}
.footer__copy {
  position: absolute;
  bottom: 0;
  right: 0;
  padding-bottom: 0.625rem;
  padding-right: 0.625rem;
  color: var(--textColor);
}

:root {
  --font-Inter: "Inter", sans-serif;
}

:root {
  /* Dark Theme */
  --veryDarkBlue-BG: hsl(230, 17%, 14%);
  --veryDarkBlue-TopBGPattern: hsl(232, 19%, 15%);
  --darkDesaturatedBlue-CardBG: hsl(228, 28%, 20%);
  --desaturatedBlue-Text: hsl(228, 34%, 66%);
  --white-Text: hsl(0, 0%, 100%);
  --dark-card-hover: hsl(228, 25%, 27%);
  /* Light Theme */
  --toggle: hsl(230, 22%, 74%);
  --white-BG: hsl(0, 0%, 100%);
  --veryPaleBlue-TopBGPattern: hsl(225, 100%, 98%);
  --lightGrayishBlue-CardBG: hsl(227, 47%, 96%);
  --darkGrayishBlue-Text: hsl(228, 12%, 44%);
  --VeryDarkBlue-Text: hsl(230, 17%, 14%);
  --light-card-hover: hsl(228, 33%, 91%);
  /* Transition */
  --transition: all 150ms ease-in-out;
  --toggleBtn: var(--toggleBtn-dark);
  --bg: var(--veryDarkBlue-BG);
  --bgTop: var(--veryDarkBlue-TopBGPattern);
  --cardBg: var(--darkDesaturatedBlue-CardBG);
  --textColor: var(--desaturatedBlue-Text);
  --textColor2: var(--white-Text);
  --cardHover: var(--dark-card-hover);
  /* Testing */
  --darkPurple: hsl(240, 28%, 14%);
  --blue: hsl(213, 73%, 15%);
  --blue-mediumS: rgba(255, 255, 255, 0.13);
  --grayishBlue: hsl(182, 26%, 72%);
  --lightGray: hsl(164, 45%, 94%);
  --grayishWhite: hsl(0, 0%, 85%);
  --githubIcon: hsl(0, 0%, 96%);
  --githubIcon-hover: hsl(0, 0%, 76%);
  --linkedinIcon: hsl(196, 100%, 43%);
  --linkedinIcon-hover: hsl(196, 100%, 33%);
}

@media (prefers-color-scheme: light) {
  :root {
    --toggleBtn: var(--toggle);
    --bg: var(--white-BG);
    --bgTop: var(--veryPaleBlue-TopBGPattern);
    --cardBg: var(--lightGrayishBlue-CardBG);
    --textColor: var(--darkGrayishBlue-Text);
    --textColor2: var(--VeryDarkBlue-Text);
    --cardHover: var(--light-card-hover);
  }
}
body.dark {
  --toggleBtn: var(--toggleBtn-dark);
  --bg: var(--veryDarkBlue-BG);
  --bgTop: var(--veryDarkBlue-TopBGPattern);
  --cardBg: var(--darkDesaturatedBlue-CardBG);
  --textColor: var(--desaturatedBlue-Text);
  --textColor2: var(--white-Text);
  --cardHover: var(--dark-card-hover);
}

body.light {
  --toggleBtn: var(--toggle);
  --bg: var(--white-BG);
  --bgTop: var(--veryPaleBlue-TopBGPattern);
  --cardBg: var(--lightGrayishBlue-CardBG);
  --textColor: var(--darkGrayishBlue-Text);
  --textColor2: var(--VeryDarkBlue-Text);
  --cardHover: var(--light-card-hover);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--blue);
}

